<template>
  <div class="edit-box">
    <div class="length-check">
      <span class="limit-len">{{ $t('settable.lnthlmt') }}</span>
            <LeetxSwitch :module="module" :value="newSnData.VerifySize" v-model="newSnData.VerifySize" class="limit-len-btn"
                :height="2">
      </LeetxSwitch>
      <span :class="{ 'forbit-color': newSnData.VerifySize == 0 }" class="SN-text">{{
                $t('settable.snLength') }}</span>
            <InputVal :module="module" :imprprChrctr="limitNumBtn" :isAllowUse="!newSnData.VerifySize == 0" :minNum="1"
                :maxNum="128" v-model:isDirty="isDirty" class="limit-len-input" v-model:sensorValue="newSnData.SnSize">
            </InputVal>
    </div>
    <div class="horizontal-line"></div>
    <div class="check-bit">
      <span class="limit-len">{{ $t("settable.characterCheck") }}</span>
      <LeetxSwitch :module="module" :value="newSnData.CharVerify" v-model="newSnData.CharVerify"
        class="limit-len-btn" :height="2">
      </LeetxSwitch>
      <span :class="{ 'forbit-color': newSnData.CharVerify == 0 }" class="SN-text">{{ $t("settable.checkStartBit") }}</span>
      <InputVal :module="module" :imprprChrctr="limitNumBtn" :isAllowUse="!newSnData.CharVerify == 0" :minNum="1"
        :maxNum="maxNum" v-model:isDirty="isDirty" class="limit-len-input" :textValid="checkStartChar()"
        v-model:sensorValue="newSnData.VerifyBit">
      </InputVal>
    </div>
    <div class="check-character">
      <span :class="{ 'forbit-color': newSnData.CharVerify == 0 }" class="limit-len">{{
        $t("settable.characterValidation")
      }}</span>
      <InputText :module="module" :imprprChrctr="limitBtn" :isAllowUse="!newSnData.CharVerify == 0"
        width="53.5vw" class="limit-len-input" :text="newSnData.VerifyChar" :maxLength="maxChar()"
        :textValid="checkChar()" validReason="" @update:modelValue="newSnData.VerifyChar = $event.text"
      ></InputText>
    </div>
    <div class="horizontal-line-two"></div>
    <div class="check-start">
      <span class="limit-len">{{ $t('settable.snStartBit') }}</span>
            <InputVal :module="module" :imprprChrctr="limitNumBtn" :minNum="1" :maxNum="startMaxNum"
                :textValid="checkStartLen()" v-model:sensorValue="newSnData.StartBit" v-model:isDirty="isDirty"
                width="17.5vw" class="snstart-input">
      </InputVal>
      <span class="character-length-text">{{ $t('settable.bitLength') }}</span>
      <InputVal :module="module" :imprprChrctr="limitNumBtn" :minNum="1" :maxNum="charLen"
                v-model:sensorValue="newSnData.Len" v-model:isDirty="isDirty"
                :textValid="newSnData.Len >= 1 && newSnData.Len <= charLen" width="17.5vw" class="character-length-input">
      </InputVal>
    </div>
    <div class="bit-dir">
      <span class="bit-dir-text">{{ $t('settable.bitDir') }}</span>
            <span class="start-right-text">{{ $t('settable.startRight') }}</span>
            <CheckCircle :module="module" v-model="newSnData.Direction" width="2rem" class="is-use-right"></CheckCircle>
    </div>
    <table class="program-map-table">
      <tr class="table-head">
        <td>{{ $t('settable.char') }}</td>
        <td>{{ $t('settable.program') }}(Pset)</td>
        <td class="delBox"></td>
      </tr>
      <div class="tr-box">
        <tr v-for="i of newSnData.CharMap.length" :key="i">
          <td>
            <inputText :module="module" :imprprChrctr="limitBtn"
                            :textValid="countCharNum(newSnData.CharMap[i - 1].Char) <= 1" validReason=""
                            :maxLength="newSnData.Len" :text="newSnData.CharMap[i - 1].Char"
                            @update:modelValue="newSnData.CharMap[i - 1].Char = $event.text" class="input-text-border" />
          </td>
          <td>
            <InputVal :module="module" :imprprChrctr="limitNumBtn" :minNum="1" :maxNum="255"
                            v-model:isDirty="isDirty" v-model:sensorValue="newSnData.CharMap[i - 1].Pset"
                            class="inputVal-border" />
          </td>
          <td class="delBox">
            <LeetxButton :module="module" class="delOne" bgcColor="transparent" width="2rem" height="2rem"
                            borderRadius="0" :useIcon="true" icon="DelDmrct" iconSize="2rem" @click="delFromArr(i)">
            </LeetxButton>
          </td>
        </tr>
      </div>

      <tr class="add-line totalline-tr">
        <td>
          <svg-loader :module="module" @click="addSnData()" class="add-btn" icon="StaticCircleAdd"
                        size="2.5rem"></svg-loader>
        </td>
        <td></td>
        <td></td>
        <td class="last-td"></td>
      </tr>
    </table>
    <LeetxButton :module="module" :enablerun="false" class="confirm-box" textColor="#fff" bgcColor="#00c155"
            width="30.125rem" height="3.75rem" fontSize="2.25rem" fontFamily="--SHSC-B" :text="$t('confirm.confirm')"
            @click="submitSnData">
    </LeetxButton>
  </div>
</template>

<script>
import sn from './sn.js'
import CheckCircle from '@/components/controls/CheckCircle.vue'
import InputText from "../input/InputText.vue"
import { busSnCfgUpload, busSnCfgDownload } from '../../api/device/device'
import { messageApi } from '../message/message.js'
import cnfrmWin from '../../dialogs/common/confirm/cnfrm.js'

export default {
  components: {
    CheckCircle,
    InputText,
  },
  props: {},
  data() {
    return {
      module: "DEV_BUS",
      isDirty: false,
      newSnData: sn(),
      global_sn_data: {},
      isAllowUse: false,
      limitBtn: ["-", ".", "space", "`", "#+=", ","],
      limitNumBtn: ["-", "."],
      need_check_edit: false,
      maxNum: 128,
    };
  },
  created() {
    this.getSnData();
    this.isAllowUse = false
  },
  methods: {
    maxChar() {
      if (this.newSnData.VerifySize == 1) {
        return this.newSnData.SnSize - this.newSnData.VerifyBit + 1
      } else {
        return 128 - this.newSnData.VerifyBit + 1
      }
    },
    checkStartLen() {
      let len = this.maxNum - this.newSnData.Len;
      return this.newSnData.StartBit <= len + 1;
    },
    checkStartChar() {
      if (!this.newSnData.CharVerify) {
        return true;
      } else {
        return this.newSnData.VerifyBit >= 1 && this.newSnData.VerifyBit <= this.maxNum;
      }
    },
    checkChar() {
      if (!this.newSnData.CharVerify) {
        return true;
      } else {
        let charSum = this.newSnData.VerifyChar.length + this.newSnData.VerifyBit - 1;
        return charSum >= 1 && charSum <= this.maxNum;
      }
    },
    //获取Sn数据
    getSnData() {
      busSnCfgUpload().then((result) => {
        if (result.status == 0) {
          this.need_check_edit = true;
          this.newSnData = result.data;
          if (this.newSnData.VerifySize == 1) {
            this.maxNum = result.data.SnSize;
          } else {
            this.maxNum = 128;
          }
          if ("CharMap" in this.newSnData == false) {
            this.newSnData.CharMap = [];
          }
          if ("Direction" in this.newSnData == false) {
            this.newSnData.Direction = 0;
          }
          if ("VerifySize" in this.newSnData == false) {
            this.newSnData.VerifySize = 0;
          }
          if ("CharVerify" in this.newSnData == false) {
            this.newSnData.CharVerify = 0;
          }
        }
        if (result.status == 9106) {
          let msg = this.$t("settable.failedToObtainSN");
          messageApi.error([msg]);
        }
        this.global_sn_data = JSON.stringify(this.newSnData);
      });
    },
    scrollToBottom() {
      const tableContainer = document.querySelector(".tr-box");
      tableContainer.scrollTop = tableContainer.scrollHeight;
    },
    addSnData() {
      let Char = "AB"
      let Pset = 1
      this.newSnData.CharMap.push({ Char, Pset })
      let verifyBit = []
      for (let i = 0; i < this.newSnData.CharMap.length; i++) {
        verifyBit.push(this.newSnData.CharMap[i].Char)
      }
      this.$nextTick(() => {
        this.scrollToBottom(); // 在DOM更新后滚动到底部
      });
      this.countCharNum()
    },
    countCharNum(char) {
      let num = 0
      this.newSnData.CharMap.forEach(item => {
        if (item["Char"] == char) {
          num = num + 1
        }
      })
      return num
    },
    checkRepeatChar() {
      let isRepeat = false
      this.newSnData.CharMap.forEach(item => {
        isRepeat = this.countCharNum(item["Char"]) > 1
      })
      return isRepeat
    },
    delFromArr(i) {
      if (this.newSnData.CharMap.length == 0) {
        return
      }
      this.newSnData.CharMap.splice(i - 1, 1)
    },
    submitSnData() {
      if (this.checkRepeatChar() == true) {
        messageApi.warning(["字符重复"])
        return
      }
      let isValidBitLen = true
      this.newSnData.CharMap.forEach(item => {
        if (item.Char.length !== this.newSnData.Len) {
          isValidBitLen = false
          return
        }
      })
      let snMaxLength = 128
      if (this.newSnData.VerifySize == 1) {
        snMaxLength = this.newSnData.SnSize
      }
      if (this.newSnData.CharVerify == 1 && this.newSnData.VerifyBit + this.newSnData.VerifyChar.length > snMaxLength + 1) {
                messageApi.warning(['校验的字符错误'])
        return
      }
      if (!isValidBitLen) {
        messageApi.warning(["程序字符长度错误"])
        return
      }
      if (this.newSnData.StartBit + this.newSnData.Len > snMaxLength + 1) {
        messageApi.warning(["SN字符长度错误"])
        return
      }
      busSnCfgDownload(this.newSnData).then(result => {
        if (result.status == 0) {
          messageApi.success([this.$t("setop.saveSuc")])
        }
        if (result.status == 9107) {
          let msg = this.$t("settable.failedToSN")
          messageApi.error([msg])
        }
        this.need_check_edit = false
      });
    },
    calMaxNum() {
      if (this.newSnData.VerifySize == 1) {
        this.maxNum = this.newSnData.SnSize
      } else {
        this.maxNum = 128
      }
    }
  },
  beforeRouteLeave(to, from, next) {
    let global_scaner_unchanged = this.global_sn_data == {} || this.global_sn_data == JSON.stringify(this.newSnData)
      if (this.isDirty == false && global_scaner_unchanged || this.$store.state.g_status.busy || this.need_check_edit == false) {
            next()
    } else {
      let title = this.$t("setcontrol.saveSet");
      let confirmText = this.$t("setcontrol.yes");
      let cancelText = this.$t("setcontrol.no");
      let msg = this.$t("setcontrol.isSaveSet");
      cnfrmWin({
        title,
        confirmText,
        cancelText,
        msg,
      }).then(() => {
          this.submitSnData()
          next()
        }).catch(() => {
          next()
        });
    }
  },
  computed: {
    charLen() {
      return this.maxNum - this.newSnData.StartBit + 1 <= 0 ? 1 : this.maxNum - this.newSnData.StartBit + 1;
    },
    enabled() {
            if (this.module in this.$store.state.f_status.permissions && this.$store.state.f_status.permissions[this.module] == 4)
                return true
            else
                return false
        },
    pointerEvent() {
      return this.enabled ? "auto" : "none";
    },
    opacity() {
      return this.enabled ? 1 : 0.5;
    },
    startMaxNum() {
      if (this.newSnData.VerifySize == 1) {
        return this.maxNum
      } else {
        return 128
      }
    },
  },
  watch: {
    "newSnData.VerifySize": function () {
      this.calMaxNum()
      this.checkStartChar()
      this.checkChar()
    },
    "newSnData.SnSize": function () {
      this.calMaxNum()
      this.checkStartChar()
      this.checkChar()
    },
    "newSnData.VerifyChar": function () {
      this.maxChar()
    },
  },
}
</script>

<style scoped>
