}

.table-head {
  border-top: 2px solid var(--C00) !important;
  border-bottom: 1px solid var(--C00) !important;
}

.save-res-line {
  position: absolute;
  top: 64.7344vh;
  left: 50%;
  transform: translateX(-50%);
  height: 2.5vh;
  width: 100%;
}

.save-res {
  font-size: var(--font1_5);
